@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;400&display=swap");

.nav-menu {
  color: #1e1e1e;
  font-family: "Inter", sans-serif;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border: none;
  transition: 0.4s;
  border-radius: 10px;

  text-decoration: none;
}
.nav-menu:hover {
  box-shadow: 0 16px 16px 0 rgba(0, 0, 0, 0.1), 0 0 48px 0 rgba(0, 0, 0, 0.19);
}

.flex-disp {
  display: flex;
  justify-content: space-evenly;

  flex-wrap: wrap;
  padding-top: 0.8rem;
  background-color: #e0cba8;
  padding-bottom: 0.8rem;
  border-bottom-left-radius: 32px;
  border-bottom-right-radius: 32px;
  width: 100%;
  position: fixed;
  overflow: hidden;
}
